.checkpoint-active {
  filter: saturate(1);
  animation: sat 2s cubic-bezier(.8,0,.2,1) alternate infinite;
  pointer-events: none;
}

/* style to diminish inactive checkpoints */
img[src*="checkpoint-inactive"] {
  animation: diminishing 1s  forwards;
}

@keyframes sat {
  0%, 100%  { filter: saturate(0); }
  30%, 50%, 70% {filter: saturate(.3);}
  20%, 40%, 60%, 80%  { filter: saturate(1); }
}

@keyframes diminishing {
  from  { transform: scale(1); }
  to  { transform: scale(.9); }
}