.slick-slider {
  /* margin: 30px auto 50px; */
  height: 100%;
}
.slick-dots {
  position: absolute;
  top: 58px;
  height: 0;
}
.slick-dots li {
  width: 15px;
  height: 15px;
}
.slick-dots li button:before {
  font-size: 10px;
  color: white;
  opacity: .5;
}
.slick-dots li.slick-active button:before {
  opacity: 1;
  font-size: 13px;
  color: white;
  text-shadow: 0 0 1px #fff, 0 0 5px #fff;
}

