.slick-prev {
  z-index: 100;
  width: 7vw;
  height: 7vw;
  border-radius: 3.5vw;
  left: 10px !important;
  background-color: white;
  opacity: 1;
  transition: .5s;
}
.slick-next {
  z-index: 100;
  width: 7vw;
  height: 7vw;
  border-radius: 3.5vw;
  right: 10px !important;
  /* background-color: white; */
  animation: simpleButtonGlowing 1s ease-in-out reverse infinite;
  opacity: 1;
  transition: .5s;
}

.arrowImg {
  width: 100%;
  height: 100%;
  background-color: white;
  border-radius: 50%;
}

