.stampArea{
    position: fixed;
    top:13px;
    left:15px;
    width:80%;
    z-index: 9997;
}

.stampAreaSmall{
    position: fixed;
    top:16px;
    left:15px;
    width:80%;
    z-index: 9997;
}

.stampImage{
    margin: 4px;
    width: 25px;
    z-index: 9997;
}

.stampImageSmall{
    margin: 3px;
    width: 20px;
    z-index: 9997;
}

.active {
    filter: saturate(1);
    z-index: 9997;
}

.inactive {
    filter: saturate(0)
}

.stamp{
    width: 170px;
    display: inline-block;
    vertical-align: middle
}

.stampSmall{
    width: 135px;
    display: inline-block;
    vertical-align: middle
}

.text{
    position: fixed;
    font-size: 18px;
    line-height: 32px;
    color: white;
    background-color: rgba(0,255,0,0)
}

.textSmall{
    position: fixed;
    font-size: 14px;
    line-height: 26px;
    color: white;
    background-color: rgba(0,255,0,0)
}
