.Teaser {
  position: fixed;
  width: 100vw;
  font-family: 'Raleway', 'Noto Sans JP', sans-serif;
  text-align: center;
  color: white;
  text-shadow: 0 0 100px #fff;
}

.Countdown{
  position: relative;
  margin: 0vw auto;
  padding-bottom: 0vh;
  font-family: 'Abel', sans-serif;
}

.Countdown-col{
  display: inline-block;
}

.Countdown-col-element{
  display: inline-block;
  margin: 0 2vw;
  display: flex;
  flex-direction: column;
}

.cd_number{
  font-size: 15vw;
}

.cd_number_ls{
  font-size: 8vw;
}

.cd_metrics{
  font-size: 1.5vw;
  font-family: 'Noto Sans JP', sans-serif;
  font-weight: 400;
  letter-spacing: .3em;
}

.cd_metrics_ls{
  font-size: .8vw;
  font-family: 'Noto Sans JP', sans-serif;
  font-weight: 400;
}

.cd_backImg{
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  margin: 0vw;
  padding: 0vw;
  z-index: -9999;
  background: #fff url('../images/app-background.jpg') no-repeat;
  background-position: center 0;
  overflow: hidden;

  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

.cd_title {
  position: relative;
  margin: 5vh 0 3vh 0;
  width: 80vw;
}

.cd_title_ls {
  position: relative;
  margin-top: 4vh;
  width: 45vw;
}

hr {
  display: block;
  height: 1px;
  border: 1em;
  /* background-color: white;
  height: .105vw; */
  padding: 0;
  width: 83vw;
}

.cd_textdescription {
  font-family: 'Noto Sans JP', sans-serif;
  font-size: 3vw;
  letter-spacing: .2em;
  line-height: 2em;
  font-weight: 900;
}

.cd_textdescription_ls {
  font-family: 'Noto Sans JP', sans-serif;
  font-size: 1.1vw;
  font-weight: 900;
  line-height: 1.7em;
}

/* .name{ */
  /* font-family: 'Raleway', sans-serif; */
/* } */

.cd_textcatchphrase {
  font-family: 'Noto Sans JP', sans-serif;
  font-size: 3.5vw;
  font-weight: 400;
  letter-spacing: .2em;
}

.cd_textcatchphrase_ls {
  font-family: 'Noto Sans JP', sans-serif;
  font-size: 1.5vw;
  font-weight: 400;
  margin-top: -.3vw;
}

.glowWhite {
  text-shadow: 0 0 1px #fff, 0 0 5px #fff, 0 0 200px #fff;
}

.day {
  font-size: .6em;
}