.thanksContainer {
  position: fixed;
  width: 100vw;
  font-family: 'Raleway', 'Noto Sans JP', sans-serif;
  text-align: center;
  color: white;
  text-shadow: 0 0 100px #fff;
}

.thanksBackground {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  margin: 0vw;
  padding: 0vw;
  z-index: -9999;
  background: #fff url('../images/app-background.jpg') no-repeat;
  background-position: center 0;
  overflow: hidden;

  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

.thanksTitle {
  position: relative;
  width: 80vw;
  pointer-events: none;
  width: 80vw;
}

.thanksCatchphrase {
  font-family: 'Noto Sans JP', sans-serif;
  font-weight: 400;
  letter-spacing: .2em;
  font-size: 7vw;
  margin: 5vw 0 9vw;
  letter-spacing: 0em;
}

.thanksDescription {
  font-family: 'Noto Sans JP', sans-serif;
  font-size: 3.1vw;
  letter-spacing: 0;
  line-height: 1.5em;
}

.album {
  width: 60vw;
  margin-top: 1.5vh;
  pointer-events: none;
}

.thanksTitleLs {
  position: relative;
  margin-top: 1vh;
  width: 30vw;
  pointer-events: none;
}

.thanksCatchphraseLs {
  font-family: 'Noto Sans JP', sans-serif;
  font-weight: 400;
  margin-top: -.3vw;
  font-size: 2.5vw;
  margin: 1vw 0 1.5vw;
  letter-spacing: 0em;
}

.thanksDescriptionLs {
  font-family: 'Noto Sans JP', sans-serif;
  font-size: .7vw;
  letter-spacing: 0;
  line-height: 1.5em;
}

.albumLs {
  width: 30vw;
  margin-top: 4vh;
  pointer-events: none;
}
