.shareContainer {
  position: fixed;
  width: 100vw;
  font-family: 'Raleway', 'Noto Sans JP', sans-serif;
  text-align: center;
  color: white;
  text-shadow: 0 0 100px #fff;
}

.shareBackground {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  margin: 0vw;
  padding: 0vw;
  z-index: -9999;
  background: #fff url('../images/app-background.jpg') no-repeat;
  background-position: center 0;
  overflow: hidden;

  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

.shareTitle {
  position: relative;
  margin-bottom: 3vh;
  width: 80vw;
  pointer-events: none;
  width: 60vw;
}

hr {
  display: block;
  height: 1px;
  border: 1em;
  /* background-color: white;
  height: .105vw; */
  padding: 0;
  width: 83vw;
}

.shareCatchphrase {
  font-family: 'Noto Sans JP', sans-serif;
  font-size: 2.7vw;
  font-weight: 400;
  letter-spacing: .2em;
  margin-top: -1vw;
}

.shareDescription {
  font-size: 2.7vw;
  margin-top: 7vw;
}

.engInstruction {
  color: silver;
  margin: .5em 0 .5vw 0;
  font-size: .8em;
}

.instruction {
  font-weight: bold;
}

.urlButton {
  background-color: white;
  font-weight: normal;
  width: 60vw;
  margin-top: 4vw;
  height: 10vw;
  border-radius: 5vw;
  color: black;
  animation: simpleGlowing 1s ease-in-out reverse infinite;
}

.urlText {
  position: absolute;
  width: 60vw;
  margin-top: 5vw;
  transform: translateY(-50%);
  font-size: 2.3vw;
}

@keyframes simpleGlowing {
  0% {box-shadow: 0 0 3px 8px rgba(255, 255, 255, 0), 0 0 15px 3px rgba(255, 241, 0, .5), 0 0 30px 8px rgba(242, 147, 0, .2)}
  100% {box-shadow: 0 0 3px 1px rgba(255, 255, 255, 1), 0 0 15px 3px rgba(255, 241, 0, 1), 0 0 30px 8px rgba(242, 147, 0, .5)}
}

.deviceFrame {
  width: 30vw;
  margin-top: 1.5vw;
  mask-image: linear-gradient(to bottom, rgba(0,0,0,1) 60%, rgba(0,0,0,0) 100%);
  pointer-events: none;
}

.shareTitleLs {
  position: relative;
  margin-top: 4vh;
  width: 60vw;
  pointer-events: none;
}

.shareCatchphraseLs {
  font-family: 'Noto Sans JP', sans-serif;
  font-size: 1.5vw;
  font-weight: 400;
  margin-top: -.3vw;
}

.shareDescriptionLs {
  font-family: 'Noto Sans JP', sans-serif;
  font-size: 1.3vw;
  font-weight: 900;
  line-height: 1.7em;
}

.engShareDescriptionLs {
  color: silver;
  font-size: .8em;
}

.browserSample {
  mask-image: linear-gradient(to bottom, rgba(0,0,0,1) 60%, rgba(0,0,0,0) 100%);
  width: 50%;
  pointer-events: none;
  margin-top: 1vw;
}

.qrCode {
  width: 30%;
  pointer-events: none;
  margin-top: 1vw;
}

.day {
  font-size: .6em;
}