.splashImageContainer {
  position: fixed;
  width: 100vw;
  transition: opacity .3s;

  background-color: #000000;
  background-repeat: no-repeat;
  background-position: center 0;
  background-size: cover;
}

.cautionMessage {
  position: absolute;
  margin: 15px;
  width: 100vw;
  pointer-events: none;
}

.splashTitleContainer {
  text-align: center;
}

.splashTitleImg {
  transform: translateY(-50%);
  pointer-events: none;
  width: 80%;
}

.sponsorFooter {
  position: absolute;
  bottom: -5px;
  width: 100%;
  pointer-events: none;
}