.listItem{
  font-size: 17px;
  /*background-color: #FFC800;*/
  color: white;
  border-radius: 5px;
  /* calc needs a space around its operator */
  max-width: calc(100% - 30px);
  max-height:20%;
  margin-left: 15px;
  margin-right: 15px;
  margin-top: 5px;
  margin-bottom: 5px;

  /* background: linear-gradient(180deg, #FFA600,#FFC800 10%,#FFA600 40%,#442222 100%); */

  /* ref: http://www.colorzilla.com/gradient-editor/#ff9400+0,ffd800+26,fcb000+26,5d2d00+100 */
  background: rgb(255,148,0);
  background: linear-gradient(110deg, rgba(255,148,0,1) 0%,rgba(255,216,0,1) 20%,rgba(252,176,0,1) 20%,rgba(93,45,0,1) 100%);
}


