.checkpoint-active {
  -webkit-filter: saturate(1);
          filter: saturate(1);
  -webkit-animation: sat 2s cubic-bezier(.8,0,.2,1) alternate infinite;
          animation: sat 2s cubic-bezier(.8,0,.2,1) alternate infinite;
  pointer-events: none;
}

/* style to diminish inactive checkpoints */
img[src*="checkpoint-inactive"] {
  -webkit-animation: diminishing 1s  forwards;
          animation: diminishing 1s  forwards;
}

@-webkit-keyframes sat {
  0%, 100%  { -webkit-filter: saturate(0); filter: saturate(0); }
  30%, 50%, 70% {-webkit-filter: saturate(.3);filter: saturate(.3);}
  20%, 40%, 60%, 80%  { -webkit-filter: saturate(1); filter: saturate(1); }
}

@keyframes sat {
  0%, 100%  { -webkit-filter: saturate(0); filter: saturate(0); }
  30%, 50%, 70% {-webkit-filter: saturate(.3);filter: saturate(.3);}
  20%, 40%, 60%, 80%  { -webkit-filter: saturate(1); filter: saturate(1); }
}

@-webkit-keyframes diminishing {
  from  { -webkit-transform: scale(1); transform: scale(1); }
  to  { -webkit-transform: scale(.9); transform: scale(.9); }
}

@keyframes diminishing {
  from  { -webkit-transform: scale(1); transform: scale(1); }
  to  { -webkit-transform: scale(.9); transform: scale(.9); }
}
