.listItem-blink{
  background: -webkit-linear-gradient(340deg,#000000,#000000,#e2b35a 40%,#e8d2a8 50%,#e2b35a 60%,#000000,#000000);
  background: linear-gradient(110deg,#000000,#000000,#e2b35a 40%,#e8d2a8 50%,#e2b35a 60%,#000000,#000000);
  background-size: 600% 600%;
  -webkit-animation: kirari 2.0s linear;
          animation: kirari 2.0s linear;
  animation-direction: reverse;
}

@-webkit-keyframes kirari {
  0% {background-position:0% 50%}
  100% {background-position:100% 50%}
}

@keyframes kirari {
  0% {background-position:0% 50%}
  100% {background-position:100% 50%}
}

