@-webkit-keyframes simpleTextGlowing {
  0% {text-shadow: 0 0 40px rgba(255, 255, 255, 1), 0 0 100px rgba(255, 255, 255, 1)}
  100% {text-shadow: 0 0 10px rgba(255, 255, 255, 1), 0 0 50px rgba(255, 255, 255, 1)}
}

@keyframes simpleTextGlowing {
  0% {text-shadow: 0 0 40px rgba(255, 255, 255, 1), 0 0 100px rgba(255, 255, 255, 1)}
  100% {text-shadow: 0 0 10px rgba(255, 255, 255, 1), 0 0 50px rgba(255, 255, 255, 1)}
}

@-webkit-keyframes simpleButtonGlowing {
  0% {box-shadow: 0 0 7px 3px rgba(255, 255, 255, 0), 0 0 15px 1px rgba(255, 241, 0, .5), 0 0 30px 3px rgba(242, 147, 0, .2)}
  100% {box-shadow: 0 0 7px 1px rgba(255, 255, 255, 1), 0 0 15px 1px rgba(255, 241, 0, 1), 0 0 30px 3px rgba(242, 147, 0, .5)}
}

@keyframes simpleButtonGlowing {
  0% {box-shadow: 0 0 7px 3px rgba(255, 255, 255, 0), 0 0 15px 1px rgba(255, 241, 0, .5), 0 0 30px 3px rgba(242, 147, 0, .2)}
  100% {box-shadow: 0 0 7px 1px rgba(255, 255, 255, 1), 0 0 15px 1px rgba(255, 241, 0, 1), 0 0 30px 3px rgba(242, 147, 0, .5)}
}

.slick-prev.slick-disabled:before, .slick-next.slick-disabled:before { 
  opacity: 0; /* control opacity when disabled. default value is .25;*/
  -webkit-transition: .5s;
  transition: .5s;
}
.slick-prev.slick-disabled, .slick-next.slick-disabled { 
  opacity: 0; /* control opacity when disabled. default value is .25;*/
  -webkit-transition: .5s;
  transition: .5s;
}

.slick-prev:hover, .slick-prev:focus, .slick-next:hover, .slick-next:focus {
    color: transparent;
    outline: none;
    background: transparent;
    background-color: white;
}

.slick-list, .slick-track, .slick-slide > *{
  height: 100%;
  color: white;
}

