.list{
  color: white;
  width: 100%;
  /*
  padding-top: 15px;
  padding-bottom: 15px;
  padding-left: 15px;
  padding-right: 15px;
  */
  margin-bottom: 25px;
}

.listItem-ready{
  background-color: #000000;
}

.listItem-playing{
  /*background: linear-gradient(110deg, #240900,#e2b35a 40%,#e8d2a8 50%,#e2b35a 60% , #240900);*/
  background: linear-gradient(110deg,#240900, #e2b35a,#240900);
  background-size: 600% 600%;
  animation: rotation 20s ease infinite;
  /* animation-direction: normal; */
}

.listItem-paused{
  /* background: linear-gradient(110deg, #240900,#e2b35a 40%,#e8d2a8 50%,#e2b35a 60% , #240900); */
}

@keyframes rotation {
  0%, 100%{background-position:0% 50%}
  50%{background-position:100% 50%}
}

.listItem-locked{
  color:gray;
  background-color: #000000;
}

.listItem-blink{
  background: linear-gradient(110deg,#000000,#000000,#e2b35a 40%,#e8d2a8 50%,#e2b35a 60%,#000000,#000000);
  background-size: 600% 600%;
  animation: kirari 2.0s linear;
  animation-direction: reverse;
}

@keyframes kirari {
  0% {background-position:0% 50%}
  100% {background-position:100% 50%}
}

.playerListItem {
  /* calc needs a space around its operator */
  max-width: calc(100% - 30px);
  max-height:20%;
  margin-left: 15px;
  margin-right: 15px;
  margin-top: 2px;
  margin-bottom: 2px;
}

.dummyForTouch{
  z-index: 1300 !important;
  color:gray;
  background: rgba(255,0,0,0.3);
  position: fixed;
  top: 60px;
  width: 100%;
  height: 30%;
}