.Modal-Long{
  position: fixed;
  z-index: 9999 !important;
  top: 65px;
  left: 15px;
  right: 15px;
  bottom: 20px;
  background-color: rgba(0, 0, 0, 1);
}

.Modal-Short{
  position: fixed;
  z-index: 9999 !important;
  top: 65px;
  left: 15px;
  right: 15px;
  height: 40%;
  background-color: rgba(0, 0, 0, 1);
}

.Modal-Long.glow {
  /* box-shadow: 0 0 7px 3px rgba(255, 255, 255, 1), 0 0 15px 5px rgba(255, 241, 0, .5), 0 0 30px 15px rgba(242, 147, 0, .7); */
  animation: glowing 1.5s ease-out forwards;
}

.Modal-Long.smallGlow {
  animation: smallGlowing 1.5s ease-out forwards;
}

.Modal-Long.shine {
  box-shadow: 0 0 15px 5px rgba(255, 241, 0, 1), 0 0 30px 15px rgba(242, 147, 0, 1);
  animation: dark 2s ease-out forwards;
}

.Modal-Long.revealModal {
  animation: revealModal .7s forwards;
}

@keyframes revealModal {
  from {opacity: 0}
  to {opacity: 1}
}

.shine .content {
  opacity: 0;
  animation: revealText 1s 2s forwards;
}

.Modal-Short.glow {
  animation: glowing 3s ease-in-out forwards;
}

@keyframes glowing {
  /* 0%, 100% {box-shadow: 0 0 15px 0px rgba(255, 241, 0, 0), 0 0 30px 0px rgba(242, 147, 0, 0)} */
  0%, 100% {box-shadow: 0 0 15px 0 rgba(255, 255, 255, 0), 0 0 15px 0 rgba(255, 241, 0, 0), 0 0 30px 0 rgba(242, 147, 0, 0)}
  /* 30%, 70% {box-shadow: 0 0 15px 5px rgba(255, 241, 0, 1), 0 0 30px 15px rgba(242, 147, 0, 1)} */
  20%, 60% {box-shadow: 0 0 7px 3px rgba(255, 255, 255, 1), 0 0 15px 5px rgba(255, 241, 0, .5), 0 0 30px 15px rgba(242, 147, 0, .7)}
}

@keyframes smallGlowing {
  0%, 100% {box-shadow: 0 0 15px 0 rgba(255, 255, 255, 0)}
  30%, 70% {box-shadow: 0 0 7px 3px rgba(255, 255, 255, 1)}
}

@keyframes dark {
  30% {background-color: white}
  0%, 100% {background-color: black}
}

@keyframes revealText {
  0% {opacity: 0}
  100% {opacity: 1}
}

.Overlay {
  position: fixed;
  z-index: 9999 !important;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(30, 30, 30, .7);
}

.uiArea{
  position: relative;
  text-align: right;
}

.closeButton{
  color: white;
  position: absolute;
  top:5px;
  right:5px;
  z-index: 1200;
}

.content {
  position: relative;
  margin: 0 15px;
  color: white;
  font-size: calc(8px + 1.4vw);
}

.modal-title{
  position: inherit;
  font-weight: 300;
  width: 100%;
  top: 20px;
  margin-bottom: 20px;
  text-align: center;
  font-size: 7vw;
  text-shadow: 0 0 1px #fff, 0 0 5px #fff;
}

.modal-miniTitle{
  position: inherit;
  /*margin-top: -15px;*/
  top: 20px;
  margin-bottom: 40px;
  text-align: center;
  font-weight: 300;
  /*font-size: 1.5em;*/
  font-size: 20px;
}

.modal-miniTitle-heavy{
  margin-top: -15px;
  margin-bottom: 20px;
  text-align: center;
  font-weight: 800;
  font-size: 1.5em;
}

.modal-content{
  font-size: 1.0em;
}

.modal-content-areaInfo{
  font-size: 0.95em;
}

.modal-content-listTitle{
  font-size: 1.0em;
  color:white;
}

.modal-content-listTitleSecondLevel{
  font-size: 0.9em;
}

.modal-content-jp{
  font-size: 0.9em;
  color: silver;
}

.modal-content-en{
  font-size: 0.7em;
  line-height: 1.1em;
  color: silver;
}

.modal-content-openingHour{
  margin-top: 5px;
  font-size: 0.7em;
}

.modal-subTitle span {
  font-size: .7em;
}

.modal-subTitle-small span {
  font-size: .7em;
}

.circle-white{
  color: black;
  text-align: center;
  background-color: white;
  border-radius: 50%;
  width: 12pt;
  height:12pt;
}

.scroll-modal::-webkit-scrollbar {
  -webkit-appearance: none;
}

.scroll-modal::-webkit-scrollbar:vertical {
  width: 4px;
}

.scroll-modal::-webkit-scrollbar:horizontal {
    /* height: 12px; */
  display: none;
}

.scroll-modal::-webkit-scrollbar-thumb {
  background-color: rgba(255,255,255,1);
  border-radius: 2px;
  border: 1px solid black;
}

.scroll-modal::-webkit-scrollbar-track {
  border-radius: 10px;  
  background-color: rgba(255,255,255,.1); 
}

.soundInfoTitle {
  position: absolute;
  top: 0;
  width: 100%;
  font-size: 7vw;
  transform: translateY(-50%)
}

.soundInfoContent {
  position: absolute;
  top: 0;
  font-size: 4vw;
  width: 100%;
}