.jpn {
  line-height: 6vw;
  font-size: 3.5vw;
}

.eng{
  margin-top: 2vw;
  color: silver;
  font-size: 3vw;
  line-height: 4vw;
  align-content: center;
}

.jpn-small {
  font-size: 3vw;
}

.eng-small{
  font-size: 2vw;
}

.row {
  display: flex;
}

.column {
  flex: 50%;
}

.column img {
  margin: -3vw 0 -8vw 0;
}

.startButton {
  animation: simpleSat .7s ease-in-out alternate infinite;
}

.startButtonGlow {
  background-color: white;
  width: 30vw;
  height: 30vw;
  box-shadow: 0 0 7px 1px rgba(255, 255, 255, 1), 0 0 15px 1px rgba(255, 241, 0, 1), 0 0 30px 12px rgba(242, 147, 0, .5);
  border-radius: 50%;
  animation: simpleGlowing 1s ease-in-out reverse infinite;
  margin-top: 15vw;
}

@keyframes simpleSat {
  0%  { filter: saturate(0); }
  100%  { filter: saturate(1); }
}

@keyframes simpleGlowing {
  0% {box-shadow: 0 0 7px 12px rgba(255, 255, 255, 0), 0 0 15px 1px rgba(255, 241, 0, .5), 0 0 30px 12px rgba(242, 147, 0, .2)}
  100% {box-shadow: 0 0 7px 1px rgba(255, 255, 255, 1), 0 0 15px 1px rgba(255, 241, 0, 1), 0 0 30px 12px rgba(242, 147, 0, .5)}
}

.checkpointSample {
  filter: saturate(1);
  animation: sat 2s cubic-bezier(.8,0,.2,1) alternate infinite;
  pointer-events: 'none'
}

@keyframes sat {
  0%, 100%  { filter: saturate(0); }
  30%, 50%, 70% {filter: saturate(.3);}
  20%, 40%, 60%, 80%  { filter: saturate(1); }
}

.pinCurrentSample {
  transform: rotate(90deg);
  animation: searching 1.3s ease-in-out alternate infinite;
  pointer-events: none;
}

@keyframes searching {
  0% {transform: rotate(80deg)}
  100% {transform: rotate(100deg)}
}